* {
    padding: 0;
    margin: 0;
    // text-align: center;
    font-family: "PT Serif";
}

html {
    font-size: 62.5%;
}

body {
    background: url("../images/violetcity.jpg");
    background-attachment: fixed;
}

$color-blue: #82aaff;
$color-blue1: #61dafb;
$color-blue2: #89ddff;
$color-violet: #c792ea;
$color-yellow: #ffcb6b;
$color-red: #ff5370;
$color-skin: #f07178;
$color-orange: #f78c6c;
$color-green: #c3e88d;
$color-dark: #464b5d;
$color-medium: #8f93a2;
$color-light: #b2ccd6;
$color-background: #0f111a;

.page {
    background: $color-background;
    color: $color-medium;
    opacity: 0.9;
    width: 100%;
    // height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    min-height: 100vh;
}
div {
    background: inherit;
}
.header {
    font-weight: bolder;
    font-size: 50pt;
    margin: 9rem auto;
    margin-top: 10rem;
    .username {
        font-size: 40pt;
    }
    text-align: center;
    color: $color-violet;
}

.heading {
    color: $color-violet;
}

.quote {
    font-size: 18pt;
    width: 50%;
    .quoteby {
        text-align: right;
        color: $color-violet;
        font-weight: bold;
    }
    padding: 5rem;
    border: 2px solid rgba($color: $color-violet, $alpha: 0.1);
    border-radius: 20px;
    transition: 0.5s;
    &:hover {
        transition: 0.5s;
        color: #eee;
        cursor: pointer;
        // border: 0;
        box-shadow: 0 0 3rem rgba($color: $color-violet, $alpha: 0.2);
    }
}

.aboutme {
    font-size: 17pt;
    width: 50%;
    border-radius: 0px;
    padding: 3rem;
    margin: 5rem auto;
    line-height: 1.8;
    text-align: justify;
    .heading {
        font-size: 30pt;
        font-weight: 600;
        // text-align: center;
    }
    em {
        font-weight: 100 !important;
    }
}

.projects {
    margin-bottom: 13rem;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .heading {
        font-size: 30pt;
        font-weight: 600;
        text-align: left;
        width: 100%;
    }
    .cards {
        padding: 3rem;
        display: grid;
        grid-template-columns: auto auto auto;
        // flex-direction: column;
        // align-items: center;
        .card {
            width: 300px;
            height: 150px;
            transition: 0.5s;
            border-radius: 1rem;
            padding: 2rem;
            margin: 2rem;
            border: 2px solid rgba($color: $color-violet, $alpha: 0.1);
            &:hover {
                transition: 0.5s;
                color: #eee;
                cursor: pointer;
                // border: 0;
                box-shadow: 0 0 3rem rgba($color: $color-violet, $alpha: 0.2);
            }
            .cardhead {
                font-size: 23pt;
                font-weight: bold;
                color: $color-violet;
            }
            .carddesc {
                font-size: 15pt;
            }
        }
    }
}

.footer {
    position: absolute;
    font-size: 13pt;
    bottom: 0;
    width: 100%;
    padding: 2.5rem 0 1rem 0;
    // height: 2.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    a,
    a:hover,
    a:focus,
    a:active {
        text-decoration: none;
        color: inherit;
        background: inherit;
    }
    .linkicon {
        margin: 1rem;
    }
    .link {
        font-size: 30pt;
        // color: #bbb;
        color: $color-violet;
        cursor: pointer;
        transition: 0.5s;
        &:hover {
            color: #eee;
            font-size: 35pt;
            filter: drop-shadow(
                0px 0px 1.5rem rgba($color: $color-violet, $alpha: 0.5)
            );
            transition: 0.5s;
        }
    }
    .createdbylink {
        margin-top: 1rem;
    }
    .createdby {
        transition: 0.5s;
        color: $color-violet;
        &:hover {
            transition: 0.5s;
            color: #eee;
            cursor: pointer;
            text-shadow: 0 0 1rem rgba($color: $color-light, $alpha: 0.5);
            // border: 0;
        }
    }
}

@font-face {
    font-family: "PT Serif";
    src: url("../fonts/ptSerif/pt-serif-v12-latin-regular.woff") format("woff"),
        //Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+
        url("../fonts/ptSerif/pt-serif-v12-latin-regular.ttf")
            format("truetype");
    // Chrome 4+, Firefox 3.5, Opera 10+, Safari 3â€”5
}

@media only screen and (max-width: 1150px) {
    .header {
        font-size: 40pt;
        // margin: 9rem auto;
        // margin-top: 10rem;
        .username {
            font-size: 30pt;
        }
    }
    .quote {
        font-size: 16pt;
        width: 70%;
        padding: 4rem;
        // border-radius: 20px;
    }
    .aboutme {
        font-size: 16pt;
        width: 70%;
        padding: 3rem;
        margin: 5rem auto;
        line-height: 1.8;
        .heading {
            font-size: 25pt;
        }
    }
    .projects {
        margin-bottom: 13rem;
        width: 70%;
        .heading {
            font-size: 25pt;
        }
        .cards {
            padding: 3rem;
            grid-template-columns: auto auto;
            .card {
                width: 290px;
                height: 120px;
                border-radius: 1rem;
                padding: 2rem;
                margin: 2rem;
                .cardhead {
                    font-size: 20pt;
                }
                .carddesc {
                    font-size: 15pt;
                }
            }
        }
    }
}

@media only screen and (max-width: 756px) {
    .header {
        font-size: 30pt;
        // margin: 9rem auto;
        // margin-top: 10rem;
        .username {
            font-size: 20pt;
        }
    }
    .quote {
        font-size: 15pt;
        width: 80%;
        padding: 3rem;
        // border-radius: 20px;
    }
    .aboutme {
        font-size: 15pt;
        width: 80%;
        padding: 3rem;
        margin: 5rem auto;
        line-height: 1.8;
        .heading {
            font-size: 22pt;
        }
    }
    .projects {
        margin-bottom: 13rem;
        width: 70%;
        .heading {
            font-size: 25pt;
        }
        .cards {
            padding: 3rem;
            grid-template-columns: auto;
            .card {
                width: 290px;
                height: 110px;
                border-radius: 1rem;
                padding: 2rem;
                margin: 2rem;
                .cardhead {
                    font-size: 18pt;
                }
                .carddesc {
                    font-size: 14pt;
                }
            }
        }
    }
}

@media only screen and (max-width: 400px) {
    .footer{
        .linkicon {
            margin: 0.8rem;
        }
        .link{
            font-size: 28pt
        }
    }
}